import history from './../history';
import models from './../apis/models';
import axios from 'axios';

export const createModel = (
  formValues,
  classification,
  formatArr,
  images,
  files
) => {
  return async (dispatch) => {
    formValues.modelPrice = parseInt(formValues.modelPrice);
    formValues = {
      ...formValues,
      modelClassification: classification,
      modelFormats: formatArr,
      modelImages: images,
      modelPreview: files[0],
    };

    const response = await models.post('/', formValues);

    dispatch({ type: 'CREATE_MODEL', payload: response.data });
    history.push('/');
  };
};

export const editModel = (
  formValues,
  classification,
  formatArr,
  images,
  files,
  id
) => {
  return async (dispatch) => {
    formValues.modelPrice = parseInt(formValues.modelPrice);

    if (!files) {
      formValues = {
        ...formValues,
        modelClassification: classification,
        modelFormats: formatArr,
        modelImages: images,
        _id: id,
      };
    } else {
      formValues = {
        ...formValues,
        modelClassification: classification,
        modelFormats: formatArr,
        modelImages: images,
        modelPreview: files[0],
        _id: id,
      };
    }

    const response = await models.patch(`/${id}`, formValues);

    dispatch({ type: 'CREATE_MODEL', payload: response.data });
    history.push('/');
  };
};

export const imagesToUpload = (images) => {
  return async (dispatch) => {
    dispatch({ type: 'IMAGES_TO_UPLOAD', payload: images });
  };
};

export const imagesToSubmit = (imgArr) => {
  return async (dispatch) => {
    dispatch({ type: 'IMAGES_TO_SUBMIT', payload: imgArr });
  };
};

export const filesToUpload = (files) => {
  return async (dispatch) => {
    dispatch({ type: 'FILES_TO_UPLOAD', payload: files });
  };
};

export const filesToSubmit = (fileArr) => {
  return async (dispatch) => {
    dispatch({ type: 'FILES_TO_SUBMIT', payload: fileArr });
  };
};

export const getModels = () => {
  return async (dispatch) => {
    const response = await axios.get(
      'https://hushpuppys-3d-hub-api.herokuapp.com/api/v1/models'
    );

    dispatch({ type: 'GET_MODELS', payload: response.data.data });
  };
};

export const selectModel = (model) => {
  return async (dispatch) => {
    dispatch({
      type: 'SELECTED_MODEL',
      payload: model,
    });
  };
};

export const deleteModel = (id) => {
  return async (dispatch) => {
    const response = await models.delete(`/${id}`);

    dispatch({ type: 'DELETE_MODEL', payload: response.data });
    history.push('/');
  };
};
